/* =========================
   TYPES
========================= */

interface FileItem {
    id: string;
    name: string;
    size: string;
    access: "Private" | "Shared";
}

interface FolderItem {
    id: string;
    name: string;
    sharedWith?: string[];
    folders: FolderItem[];
    files: FileItem[];
}

/* =========================
   STATIC DATA
========================= */

const documentTree: FolderItem[] = [
    {
        id: "1",
        name: "Personal Documents",
        folders: [
            {
                id: "1-1",
                name: "Identity",
                folders: [],
                files: [
                    {
                        id: "f1",
                        name: "Aadhar.pdf",
                        size: "420 KB",
                        access: "Private",
                    },
                    {
                        id: "f2",
                        name: "PAN.pdf",
                        size: "280 KB",
                        access: "Private",
                    },
                ],
            },
        ],
        files: [],
    },
    {
        id: "2",
        name: "HR Documents",
        sharedWith: ["manager@company.com"],
        folders: [
            {
                id: "2-1",
                name: "Payslips",
                folders: [],
                files: [
                    {
                        id: "f3",
                        name: "Payslip_Jan.pdf",
                        size: "190 KB",
                        access: "Shared",
                    },
                ],
            },
        ],
        files: [],
    },
];

/* =========================
   FOLDER TREE COMPONENT
========================= */

const FolderNode: React.FC<{ folder: FolderItem; level?: number }> = ({
    folder,
    level = 0,
}) => {
    const [open, setOpen] = useState(false);

    return (
        <div style={{ marginLeft: level * 20 }} className="mb-2">
            {/* Folder Row */}
            <div className="flex items-center justify-between rounded-lg px-3 py-2 hover:bg-gray-100 dark:hover:bg-white/5">
                <div
                    className="flex items-center gap-2 cursor-pointer"
                    onClick={() => setOpen(!open)}
                >
                    {folder.folders.length || folder.files.length ? (
                        open ? <ChevronDown size={16} /> : <ChevronRight size={16} />
                    ) : (
                        <span className="w-4" />
                    )}

                    <Folder size={18} className="text-brand-500" />
                    <span className="font-medium">{folder.name}</span>

                    {folder.sharedWith && (
                        <Badge size="sm" color="primary">
                            Shared
                        </Badge>
                    )}
                </div>

                {/* Actions */}
                <div className="flex gap-3 text-gray-500">
                    <Upload
                        size={16}
                        className="cursor-pointer hover:text-brand-500"
                    />
                    <Share2
                        size={16}
                        className="cursor-pointer hover:text-brand-500"
                    />
                </div>
            </div>

            {/* Children */}
            {open && (
                <div className="mt-1 space-y-1">
                    {/* Files */}
                    {folder.files.map((file) => (
                        <div
                            key={file.id}
                            className="flex items-center gap-3 ml-8 px-3 py-2 rounded-md hover:bg-gray-50 dark:hover:bg-white/5"
                        >
                            <FileText size={16} />
                            <span className="text-sm">{file.name}</span>
                            <span className="text-xs text-gray-400 ml-auto">
                                {file.size}
                            </span>
                            <Badge
                                size="sm"
                                color={file.access === "Shared" ? "primary" : "dark"}
                            >
                                {file.access}
                            </Badge>
                        </div>
                    ))}

                    {/* Subfolders */}
                    {folder.folders.map((child) => (
                        <FolderNode key={child.id} folder={child} level={level + 1} />
                    ))}
                </div>
            )}
        </div>
    );
};

/* =========================
   ALL FILES TABLE
========================= */

const getAllFiles = (folders: FolderItem[]) => {
    let files: any[] = [];

    folders.forEach((folder) => {
        folder.files.forEach((file) =>
            files.push({ ...file, folder: folder.name })
        );

        folder.folders.forEach((sub) => {
            sub.files.forEach((file) =>
                files.push({ ...file, folder: sub.name })
            );
        });
    });

    return files;
};

const AllFilesTable = () => {
    const files = getAllFiles(documentTree);

    return (
        <div className="rounded-xl border bg-white dark:bg-white/[0.03]">
            <div className="px-5 py-4 border-b">
                <h3 className="font-medium">All Documents</h3>
            </div>

            <div className="overflow-x-auto">
                <table className="w-full text-sm">
                    <thead className="bg-gray-50 dark:bg-white/5">
                        <tr>
                            <th className="text-left px-5 py-3">File</th>
                            <th className="text-left px-5 py-3">Folder</th>
                            <th className="text-left px-5 py-3">Size</th>
                            <th className="text-left px-5 py-3">Access</th>
                        </tr>
                    </thead>
                    <tbody>
                        {files.map((file) => (
                            <tr
                                key={file.id}
                                className="border-t hover:bg-gray-50 dark:hover:bg-white/5"
                            >
                                <td className="px-5 py-3 flex items-center gap-2">
                                    <FileText size={16} />
                                    {file.name}
                                </td>
                                <td className="px-5 py-3">{file.folder}</td>
                                <td className="px-5 py-3">{file.size}</td>
                                <td className="px-5 py-3">
                                    <Badge
                                        size="sm"
                                        color={file.access === "Shared" ? "primary" : "dark"}
                                    >
                                        {file.access}
                                    </Badge>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </div>
    );
};

